<template lang="html">
  <div id="edit">
      <p class="title">基本信息</p>

       <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
           <Form-item label="员工姓名" prop="userName">
               <span>{{formValidate.userName}}</span>
           </Form-item>
            <Form-item label="联系电话" prop="phone">
                <Input v-model="formValidate.phone" placeholder="请输入联系电话" class='text fl'></Input>
            </Form-item>
            <Form-item label="移动电话" prop="mobile">
                <Input v-model="formValidate.mobile" placeholder="请输入移动电话" class='text fl'></Input>
            </Form-item>
            <Form-item label="所属部门" prop="deptName">
                <Input v-model="formValidate.name" placeholder="请输入所属部门" class='text fl'></Input>
                <span class='fl ML10 info'>[经销商智能营销事业部]</span>
            </Form-item>
       </Form>
  </div>
</template>

<script>
export default {
    data() {
        return  {
            formValidate: {
                userName: '',
                phone: '',
                mobile: '',
                deptName: ''
            }
        }
    }
}
</script>

<style lang="scss">
#edit {
    padding: 50px 30px;

    .title {
        font-size: 14px;
        color: #354052;
        line-height: 14px;
        padding-bottom: 15px;
    }

    .info {
        font-size: 12px;
        color: #7B8497;
    }

    .text {
        width: 400px;
        height: 38px;
    }
}
</style>



